import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import { ReactComponent as QuoteIcon } from '../../assets/quote-icon2.svg';
import { useRef, useState } from "react";

const CEOTestimonialSlider = () => {
    const sliderRef = useRef(null);
    const [blink, setBlink] = useState(true);

    const settings = {
        slidesToShow: 1,
        slidesToScroll: 1,
        infinite: true,
        dots: false,
        arrows: false,
        pauseOnHover: false,
        pauseOnFocus: false
    };

    const goToPrevSlide = () => {
        if (sliderRef.current) {
            sliderRef.current.slickPrev();
        }
    };

    const goToNextSlide = () => {
        if (sliderRef.current) {
            sliderRef.current.slickNext();
            setBlink(false);
        }
    };

    return (
        <div className="testimonialBoxContainer ceoTestimonial">
            <Slider ref={(slider) => (sliderRef.current = slider)} {...settings}>
                {testimonials.map((testimonial, index) => (
                    <div className="testimonialItem" key={index}>
                        <QuoteIcon width="54" height="42" loading="lazy" />
                        <div>
                            <h4>{testimonial.title}</h4>
                            {testimonial.content.map((paragraph, idx) => (
                                <p key={idx}>{paragraph}</p>
                            ))}
                        </div>
                    </div>
                ))}
            </Slider>
            <div className="testimonialNavigation">
                <button className="testimonial_button_prev" onClick={goToPrevSlide}>
                    <span className="arrow"></span>
                </button>
                <button
                    className={`testimonial_button_next ${blink ? "blinking" : ""}`}
                    onClick={goToNextSlide}
                >
                    <span className="arrow"></span>
                </button>
            </div>
        </div>
    );
};

const testimonials = [
    {
        title: "M definesc integritatea, evoluia i pasiunea.",
        content: [
            "Valorile mele organizationale mai includ: o viziune strategic, comunicare eficient, orientare spre rezultate i, mai presus de orice, focus pe oameni."
        ]
    },
    {
        title: "Parcursul meu profesional",
        content: [
            "Pe parcursul ultimului deceniu, cariera mea s-a dezvoltat constant n sfera antreprenoriatului.",
            "Experiena antreprenorial m-a ajutat s neleg n profunzime att oportunitile, ct i obstacolele cu care se confrunt afacerile mici i mijlocii."
        ]
    },
    {
        title: "Parteneriat potenial profitabil",
        content: [
            "Un parteneriat bazat pe integritate i implicare din ambele pri are potenialul de a fi profitabil i durabil."
        ]
    },
    {
        title: "Cum a luat RUMO natere?",
        content: [
            "RUMO a luat natere n mod firesc, pe parcursul dezvoltrii mele n antreprenoriat."
        ]
    },
    {
        title: "Viziunea RUMO",
        content: [
            "n urmtorii cinci ani, ne dorim s devenim un partener esenial pentru companiile mici i mijlocii, contribuind semnificativ la creterea acestora.",
            "Astzi, n calitate de CEO al RUMO, m concentrez pe meninerea i dezvoltarea unei echipe de profesioniti dedicat performanei."
        ]
    }
];

export default CEOTestimonialSlider;
